<template>
	<view>
		<view class="total"></view>
		<view class="fix-top flex">
		    <view class="stock flex flex-column mr-20" style="width: 200rpx;">
		        <view class="text">商品总数</view>
		        <view class="number">{{form.goods_total_count}}</view>
		    </view>
			<view class="good flex flex-column mr-20" style="width: 200rpx;">
			    <view class="text">工艺总数</view>
			    <view class="number">{{form.workmanship_total_count}}</view>
			</view>
			<view class="good flex flex-column" style="width: 282rpx">
			    <view class="text">成交总额</view>
			    <view class="number">￥{{form.total_transaction_price}}</view>
			</view>
		</view>
		<!-- 搜索 -->
		<view class="container search" style="margin-top: 40rpx;">
			<view class="flex">
				<uni-easyinput class="input" v-model="search_name" placeholder="请输入客户名称"></uni-easyinput>
				<button class="btn-search" @click="search">查询</button>
			</view>
		</view>
		<!-- 列表 -->
		<view class="container list">
			<view class="item" v-for="(item,index) in list" :key="item.id">
				<view class="flex mb-10">
					<view class="title grey">销售员</view>
					<view class="value">{{item.username}}</view>
				</view>
				<view class="flex mb-10">
					<view class="title grey">客户名称</view>
					<view class="value">{{item.name}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">商品数量</view>
					<view class="value">{{item.total_count}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">商品总价</view>
					<view class="value">￥{{item.total_money}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">工艺数量</view>
					<view class="value">{{item.workmanship_total_count}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">工艺总价</view>
					<view class="value">￥{{item.workmanship_total_money}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">订单总价</view>
					<view class="value">￥{{item.order_total_money}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">成交价</view>
					<view class="value">￥{{item.transaction_price}}</view>
				</view>
				<view class="flex mb-10 last">
					<view class="title grey">已收金额</view>
					<view class="value">￥{{item.received_amount}}</view>
				</view>
				<view class="arrow flex flex-column" @click="detail(item.id)">
					<view>详情</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				uid:'',
				list: [],
				form:{},
				search_name:'',
				page:0,
			}
		},
		onLoad() {
			this.page = 0
			 this.uid = this.common.get("uid")
			// 列表接口
			this.listInterface()
		},
		//上拉触底函数
		onReachBottom(){
			// 出库记录接口
			this.http({
				url: this.api.order_report,
				method: "POST",
				data: {
					uid: this.common.get("uid"),
					search: this.search_name,
					page:this.page
				},
				success: res => {
					this.page+=1
					if(res.list.length == 0) {
						uni.showToast({
							title:'没有更多了',
							icon:'none',
							duration: 2000
						})
					} else {
						this.list = this.list.concat(res.list)
						this.form.goods_total_count = res.goods_total_count
						this.form.total_transaction_price = res.total_transaction_price
						this.form.workmanship_total_count = res.workmanship_total_count
					}
				}, 
			})
		},
		methods: {
			// 详情按钮点击
			detail(eve){
				uni.navigateTo({
					url:'/pages/report/details/details?data='+encodeURIComponent(JSON.stringify(eve))
				})
			},
			search() {
				this.page = 0
				this.listInterface()
			},
			// 列表接口
			listInterface() {
				this.http({
					url: this.api.order_report,
					method: "POST",
					data: {
						uid: this.common.get("uid"),
						search:this.search_name,
						page:this.page
					},
					success: res => {
						this.page+=1
						if(res.list.length == 0) {
							uni.showToast({
								title:'没有数据',
								icon:'none',
								duration: 2000
							})
							this.list = res.list
						} else {
							this.list = res.list
						}
						this.form.goods_total_count = res.goods_total_count
						this.form.total_transaction_price = res.total_transaction_price
						this.form.workmanship_total_count = res.workmanship_total_count
					}
				})
			},
		}
	}
</script>

<style lang="scss">
.total {
	width: 750rpx;
	height: 170rpx;
	background: $color-main;
}
.list {
	.item {
		.grey {
			width: 112rpx;
			text-align-last: justify;
		}
	}
}
.fix-top {
	position: absolute;
	width: 750rpx;
	height: 270rpx;
	padding: 30rpx;
	box-sizing: border-box;
	top: 0;
	.flex {
		// width: 346rpx;
		height: 160rpx;
		background: $color-f;
		box-shadow: 0rpx 2rpx 16rpx 2rpx rgba(179, 175, 175, 0.12);
		border-radius: 8rpx;
		.text {
			font-size: 24rpx;
			color: $color-9;
		}
		.number {
			font-size: 40rpx;
			color: $color-3;
			font-weight: bold;
			margin-top: 10rpx;
		}
	}
}
.btn-color {
	width: 120rpx!important;
	height: 64rpx;
	line-height: 64rpx;
	background: $color-b5;
}
.search .uni-date-editor--x .uni-date__icon-clear {
	top: -4rpx;
}
.arrow_jt {
	position: absolute;
	right: 30rpx;
	bottom: 30rpx;
	image {
		width: 29.96rpx;
		height: 16rpx;
	}
}
</style>
